  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  padding: 2em;
  max-height: 100vh;
  width: 100%;
  background: linear-gradient(
    to bottom left,
    var(--primary-color),
    var(--secondary-color)
  );

  border-radius: 10px;
}

.Input_input-box__3j1Qs {
  height: 50px;
  font-size: 1.3rem;
  width: calc(100% - 50px);
  border-radius: 5px;
  padding: 1em;
  outline: none;
  background-color: #262729;
  transition: box-shadow 200ms ease;
  color: white;
  border: none;
}

